.Login {
	background-color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    *{
        text-decoration: none;
    }

    .loginLogo {
        display: flex;
        align-items: center;
        margin: 30px 0;
    }

    .loginLogoImage {
        color: #ff9f00;
    }

    .loginLogoTitle {
        color: black;
    }

    .loginContainer {
        width: 300px;
        height: fit-content;
        display: flex;
        flex-direction: column;
        border-radius: 5px;
        border: 1px solid lightgray;
        padding: 20px;
    }

    .loginContainer > h1 {
        font-weight: 500;
        margin-bottom: 20px;
    }

    .loginContainer > form > label {
        margin-bottom: 5px;
    }

    .loginContainer > form > label > input {
        height: 30px;
        margin-bottom: 10px;
        background-color: white;
        width: 98%;
    }

    .loginContainer > p {
        margin-top: 15px;
        font-size: 12px; 
    }

    .loginSignInBtn {
        background: #ff9f00;
        border-radius: 2px;
        width: 100%;
        height: 30px;
        border: 1px solid;
        margin-top: 10px;
        border-color: #a88734 #9c7e31 #846a29;
    }

    .loginRegisterBtn {
        border-radius: 2px;
        width: 100%;
        height: 30px;
        border: 1px solid;
        margin-top: 10px;
        border-color: darkgrey;
    }
}
