.Subtotal {
	display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 280px;
    height: 100px;
    padding: 20px;
    background-color: #f3f3f3;
    border: 1px solid #dddddd;
    border-radius: 3px;

    .subtotalGift {
        display: flex;
        align-items: center;
    }

    .subtotalGift > input {
        margin-right: 5px;
    }

    
}

.Subtotal > button {
    background: #ff9f00;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}