.Product {
	display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;

    .productRating {
        display: flex;
    }

    img {
        max-height: 200px;
        width: 100%;
        object-fit: contain;
        margin: 20px 0 0 15px;
    }

    button {
        background: #ff9f00;
        border: 1px solid;
        margin-top: 10px;
        border-color: #a88734 #9c7e31 #846a29;
        color: #111;
    }

    .productPrice {
        margin-top: 5px;
    }

    .productInfo {
        height: 100px;
        margin-bottom: 15px;
    }
}
